@import "./offer";

.calculator h3 {
  font-size: 2.2rem;
  font-weight: 500;
  line-height: 1;
  margin: 0;
  margin-bottom: -0.4rem;
}

.calculator__step {
  &--2 {
    margin-top: 3.8rem;
  }
}

.calculator__config {
  display: flex;
}

.calculator__params {
  position: relative;
  width: 600px;
  flex-shrink: 2;
}

.calculator__options {
  background-color: white;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.calculator__field-container {
  margin-top: 3rem;
}

.calculator__label,
.calculator__limits {
  color: $text-color-calculator;
  display: block;
  margin: 0;
  line-height: 1;
}

.calculator__label {
  font-size: 1.6rem;
  line-height: 1;
  font-weight: normal;
  margin-bottom: 1.1rem;
  margin-top: -5px;
}

.calculator__field {
  align-items: center;
  background-color: white;
  display: flex;
  position: relative;

  &--input {
    font-size: 1.8rem;
    justify-content: center;
  }

  &--select,
  &--option {
    cursor: pointer;
  }

  &--option {
    border-top: none;
    border-radius: 0;
    border-bottom-color: #c1c2ca;
  }

  &--first {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  &--last {
    border-bottom-color: $text-color-primary;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  &:hover {
    background-color: rgba($blue-color-main, 0.05);
  }

  &:focus,
  &--focused {
    box-shadow: $focus-shadow;
    z-index: 2;
  }

  &--error#{&} {
    background-color: rgba(red, 0.2);
  }
}

.calculator__field-error {
  bottom: 2px;
  color: crimson;
  font-size: 1.2rem;
  left: 50%;
  line-height: 1.2rem;
  transform: translateX(-50%);
  position: absolute;
}

.calculator__menu {
  position: absolute;
  right: 22px;
  top: 24px;

  &--visible {
    transform: scaleY(-1);
  }
}

.calculator__input {
  appearance: none;
  background-color: transparent;
  border: none;
  font: inherit;
  height: 100%;
  padding: 0;

  &::-ms-clear {
    display: none;
  }
}

.calculator__input-text {
  user-select: none;
}

.calculator__input-clone {
  color: transparent;
  font: inherit;
  pointer-events: none;
  position: absolute;
  z-index: -1000;
}

.calculator__limits {
  display: flex;
  justify-content: space-between;
  font-size: 1.4rem;
  line-height: 1;
  padding-bottom: 1px;
  margin-top: 0.9rem;
}

.calculator__offer-container {
  align-self: flex-start;
  margin-left: 70px;
  width: 500px;
}

.calculator__field-button {
  color: $text-color-primary;
  height: 16px;
  margin: 0 24px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;

  &::before,
  &::after {
    background-color: currentColor;
    content: "";
    display: none;
    position: absolute;
  }

  &::before {
    display: block;
    width: 100%;
    height: 2px;
    top: 50%;
    transform: translateY(-50%);
  }

  &--inc {
    right: 0;

    &::after {
      display: block;
      height: 100%;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      width: 2px;
    }
  }

  &--dec {
    left: 0;
  }
}

.calculator__checkbox-container {
  display: flex;
  align-items: baseline;
  font-size: 1.6rem;
  letter-spacing: -0.005em;
  margin-top: 2.5rem;
}

.calculator__checkbox-text {
  margin-left: 0.9rem;
}

.calculator-modal {
  background-color: $bg-color-primary;
  padding: 60px;
  text-align: center;

  p {
    margin: 0;
  }

  & > p {
    margin-top: -0.7rem;
  }
}

.calculator-modal__manager {
  color: #394959;
  font-size: 1.6rem;
  letter-spacing: -0.005em;
  line-height: 2.2rem;
  margin: 0 auto;
  margin-bottom: -0.6rem;
  margin-top: 1.2rem;
  width: 300px;
}

.calculator__request {
  margin: 0 auto;
  margin-top: 6.5rem;
  max-width: 770px;
}

@include mq(tablet) {
  .calculator__step h3 {
    font-size: 1.8rem;
  }

  .calculator__config {
    flex-direction: column;
  }

  .calculator__params {
    width: 100%;
  }

  .calculator__field-container {
    margin-top: 2.6rem;
  }

  .calculator__field {
    &--input {
      font-size: 1.6rem;
    }
  }

  .calculator__limits {
    margin-bottom: -0.6rem;
  }

  .calculator__offer-container,
  .calculator__request {
    max-width: 678px;
    width: 100%;
  }

  .calculator__offer-container {
    margin: 0 auto;
    margin-top: 3.7rem;
  }

  .calculator__request {
    margin-top: 5.8rem;
  }
}

@include mq(phone) {
  .calculator__step h3 {
    font-size: 1.6rem;
  }

  .calculator__step--2 {
    margin-top: 2.8rem;
  }

  .calculator__field-container {
    margin-top: 2.1rem;
  }

  .calculator__menu {
    right: 14px;
  }

  .calculator__checkbox-container {
    font-size: 1.4rem;
    align-items: center;
    margin-top: 2.1rem;
  }

  .calculator__offer-container,
  .calculator__request {
    max-width: 450px;
  }

  .calculator__offer-container {
    margin-top: 3rem;
  }

  .calculator__request {
    margin-top: 4.5rem;
  }

  .calculator-modal {
    padding: 40px 26px;

    p {
      margin: 0;
    }

    & > p {
      font-size: 1.8rem;
      margin: 0 auto;
      margin-top: -0.5rem;
      width: 85%;
      max-width: 400px;
    }
  }

  .calculator-modal__manager {
    margin: 0 -1%;
    margin-top: 1.4rem;
    width: 102%;
    max-width: 400px;
  }
}
